import {Box, Text} from "@chakra-ui/react"
import {
    MenuContent,
    MenuItem,
    MenuRoot,
    MenuTrigger,
} from "@/components/ui/menu"

import { LANGUAGE_VERSIONS } from "../constants/pro-languages"

interface LanguageSelectorProps {
    language: string;
    onSelect: (language: string) => void;
}

const LanguageSelector = ({language,onSelect}: LanguageSelectorProps) => {

    const languages = Object.entries(LANGUAGE_VERSIONS);

    return (
        <Box className="flex gap-1">
            <Text mb={2} fontSize='xl'>Language: </Text>
            <MenuRoot >
            <MenuTrigger  paddingBottom={2}>
                <Text cursor="pointer"  px={3} py={1} border="1px solid" borderColor="gray.300" borderRadius="md" fontSize='md'>
                    {language || "Choose a language"}
                </Text>
            </MenuTrigger>

                <MenuContent px={3} py={1}>
                {languages.map(([language, version]) => (
                        <MenuItem px={3} py={1} key={language} value="..." onClick={() => onSelect(language)}>{language}
                        &nbsp;
                        <Text as="span" color="gray.500" fontSize="sm">
                            {version}
                        </Text>
                        </MenuItem>
                    ))}
                    
                </MenuContent>
            </MenuRoot>
        </Box>
    )
}

export default LanguageSelector